// Breakpoints.

@include breakpoints((xlarge: (1281px, 1680px),
        large: (981px, 1280px),
        medium: (737px, 980px),
        small: (481px, 736px),
        xsmall: (361px, 480px),
        xxsmall: (null, 360px)));

// Mixins.

@mixin inner {
    >.inner {
        @include padding(4em, 0);
        margin: 0 auto;
        max-width: _size(inner);
        width: calc(100% - 6em);

        @include breakpoint('<=small') {
            @include padding(3em, 0);
            width: calc(100% - 3em);
        }
    }
}

